.layout {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(4, 1fr);
  gap: 5px;

  height: 600px;
  /* animation-duration: 10s; */

  overflow: hidden;
  border-radius: 5px;
}

.item {
  position: relative;
  background-color: #ffffff59;
  border-radius: 10px;

  cursor: pointer;
  overflow: hidden;

  transition: ease 0.3s;
  transform: scale(0.98);
  background-repeat: no-repeat;
}

.item:hover {
  opacity: 1;
  transform: scale(1);
}

