.--1 {
  grid-row: 1 / 3;
  background-image: url('../../img/sedona.png');
  background-size: cover;
  background-position: -191px 0;
}

.--2 {
  /* background-image: url('../../img/duplex.png'); */
  background-size: cover;
}


.--3 {
  grid-row: 2 / 3;
}

.--4 {
  grid-column: 3 / 5;
  background-image: url('../../img/duplex.png');
  background-size: cover;
}

.--5 {
  grid-column: 2 / 3;
  grid-row: 3 / 5;

  background-image: url('../../img/cat-energy.png');
  background-size: cover;
  background-position: 0 0;
}

.--6 {
  grid-row: 3 / 4;
}

.--7 {
  grid-row: 4 / 5;
}

.--8 {
  grid-column: 3 / 4;
  grid-row: 2 / 4;
}

.--11 {
  grid-column: 3 / 5; 
}